import { stringify } from '@angular/compiler/src/util';
import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { ComponentDataService } from '../../../core/component/component-data.service';
import {
  ComponentBaseStyle,
  ComponentBaseData,
} from '../../../types/component-type';
import calculateComponentPositonAndSize from '../../../utils/calculateComponentPositonAndSize';

@Component({
  selector: 'shape',
  templateUrl: './shape.component.html',
  styleUrls: ['./shape.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ShapeComponent implements OnInit {
  @Input() active: boolean = false;
  @Input() element: ComponentBaseData;
  @Input() defaultStyle: ComponentBaseStyle;
  @Input() index: number;
  cursors = {};
  pointList: string[] = ['lt', 't', 'rt', 'r', 'rb', 'b', 'lb', 'l']; // å«ä¸ªæ¹å
  initialAngle = {
    // æ¯ä¸ªç¹å¯¹åºçåå§è§åº¦
    lt: 0,
    t: 45,
    rt: 90,
    r: 135,
    rb: 180,
    b: 225,
    lb: 270,
    l: 315,
  };
  angleToCursor = [
    // æ¯ä¸ªèå´çè§åº¦å¯¹åºçåæ 
    { start: 338, end: 23, cursor: 'nw' },
    { start: 23, end: 68, cursor: 'n' },
    { start: 68, end: 113, cursor: 'ne' },
    { start: 113, end: 158, cursor: 'e' },
    { start: 158, end: 203, cursor: 'se' },
    { start: 203, end: 248, cursor: 's' },
    { start: 248, end: 293, cursor: 'sw' },
    { start: 293, end: 338, cursor: 'w' },
  ];
  constructor(public componentDataService: ComponentDataService) {}

  ngOnInit(): void {}

  getShapeStyle(style: ComponentBaseStyle) {
    const result = { ...style };
    if (result.width) {
      result.width += 'px';
    }

    if (result.height) {
      result.height += 'px';
    }

    if (result.top) {
      result.top += 'px';
    }

    if (result.left) {
      result.left += 'px';
    }

    if (result.rotate) {
      result['transform'] = 'rotate(' + result.rotate + 'deg)';
    }

    return result;
  }

  handleMouseDownOnShape(e) {
    if (this.element.component != 'v-text') {
      e.preventDefault();
    }

    e.stopPropagation();
    this.componentDataService.curComponent = this.element;
    this.componentDataService.curComponentIndex = this.index;
    this.cursors = this.getCursor(); // æ ¹æ®æè½¬è§åº¦è·ååæ ä½ç½®
    const pos = { ...this.defaultStyle };
    const startY = e.clientY;
    const startX = e.clientX;
    // å¦æç´æ¥ä¿®æ¹å±æ§ï¼å¼çç±»åä¼åä¸ºå­ç¬¦ä¸²ï¼æä»¥è¦è½¬ä¸ºæ°å¼å
    const startTop = Number(pos.top);
    const startLeft = Number(pos.left);

    // å¦æåç´ æ²¡æç§»å¨ï¼åä¸ä¿å­å¿«ç§
    let hasMove = false;
    const move = (moveEvent) => {
      hasMove = true;
      const curX = moveEvent.clientX;
      const curY = moveEvent.clientY;
      pos.top = curY - startY + startTop;
      pos.left = curX - startX + startLeft;

      // ä¿®æ¹å½åç»ä»¶æ ·å¼
      this.componentDataService.$shapeStyle.next(pos);
      // ç­æ´æ°å®å½åç»ä»¶çæ ·å¼å¹¶ç»å¶å°å±å¹ååå¤æ­æ¯å¦éè¦å¸é
      // å¦æä¸ä½¿ç¨ $nextTickï¼å¸éåå°æ æ³ç§»å¨
      // this.$nextTick(() => {
      //   // è§¦ååç´ ç§»å¨äºä»¶ï¼ç¨äºæ¾ç¤ºæ çº¿ãå¸éåè½
      //   // åé¢ä¸¤ä¸ªåæ°ä»£è¡¨é¼ æ ç§»å¨æ¹å
      //   // curY - startY > 0 true è¡¨ç¤ºåä¸ç§»å¨ false è¡¨ç¤ºåä¸ç§»å¨
      //   // curX - startX > 0 true è¡¨ç¤ºåå³ç§»å¨ false è¡¨ç¤ºåå·¦ç§»å¨
      //   eventBus.$emit('move', curY - startY > 0, curX - startX > 0);
      // })
      this.componentDataService.notification.next({
        event: 'move',
        value: [curY - startY > 0, curX - startX > 0],
      });
    };

    const up = () => {
      hasMove && this.componentDataService.recordSnapshot();
      // è§¦ååç´ åæ­¢ç§»å¨äºä»¶ï¼ç¨äºéèæ çº¿
      // eventBus.$emit('unmove')
      this.componentDataService.notification.next({
        event: 'unmove',
      });
      document.removeEventListener('mousemove', move);
      document.removeEventListener('mouseup', up);
    };

    document.addEventListener('mousemove', move);
    document.addEventListener('mouseup', up);
  }

  selectCurComponent(e) {
    e.stopPropagation();
    e.preventDefault();
    this.componentDataService.hideContextMenu();
  }

  handleRotate() {}

  handleMouseDownOnPoint(point: string, e) {
    const downEvent = window.event;
    downEvent.stopPropagation();
    downEvent.preventDefault();

    const style = { ...this.defaultStyle };
    const center = {
      x: style.left + style.width / 2,
      y: style.top + style.height / 2,
    };

    // è·åç»å¸ä½ç§»ä¿¡æ¯
    const editorRectInfo = document
      .querySelector('#editor')
      .getBoundingClientRect();

    // å½åç¹å»åæ 
    const curPoint = {
      x: e.clientX - editorRectInfo.left,
      y: e.clientY - editorRectInfo.top,
    };

    // è·åå¯¹ç§°ç¹çåæ 
    const symmetricPoint = {
      x: center.x - (curPoint.x - center.x),
      y: center.y - (curPoint.y - center.y),
    };

    // æ¯å¦éè¦ä¿å­å¿«ç§
    let needSave = false;
    let isFirst = true;
    const move = (moveEvent) => {
      // ç¬¬ä¸æ¬¡ç¹å»æ¶ä¹ä¼è§¦å moveï¼æä»¥ä¼æâåç¹å»ç»ä»¶ä½æªç§»å¨ï¼ç»ä»¶çå¤§å°å´æ¹åäºâçæåµåç
      // å æ­¤ç¬¬ä¸æ¬¡ç¹å»æ¶ä¸è§¦å move äºä»¶
      if (isFirst) {
        isFirst = false;
        return;
      }

      needSave = true;
      const curPositon = {
        x: moveEvent.clientX - editorRectInfo.left,
        y: moveEvent.clientY - editorRectInfo.top,
      };

      calculateComponentPositonAndSize(point, style, curPositon, {
        center,
        curPoint,
        symmetricPoint,
      });

      this.componentDataService.$shapeStyle.next(style);
    };

    const up = () => {
      document.removeEventListener('mousemove', move);
      document.removeEventListener('mouseup', up);
      needSave && this.componentDataService.recordSnapshot();
    };

    document.addEventListener('mousemove', move);
    document.addEventListener('mouseup', up);
  }

  getPointStyle(
    point: string
  ): {
    marginLeft: string;
    marginTop: string;
    left: string;
    top: string;
    cursor: string;
  } {
    const { width, height } = this.defaultStyle;
    const hasT = /t/.test(point);
    const hasB = /b/.test(point);
    const hasL = /l/.test(point);
    const hasR = /r/.test(point);
    let newLeft = 0;
    let newTop = 0;

    // åä¸ªè§çç¹
    if (point.length === 2) {
      newLeft = hasL ? 0 : width;
      newTop = hasT ? 0 : height;
    } else {
      // ä¸ä¸ä¸¤ç¹çç¹ï¼å®½åº¦å±ä¸­
      if (hasT || hasB) {
        newLeft = width / 2;
        newTop = hasT ? 0 : height;
      }

      // å·¦å³ä¸¤è¾¹çç¹ï¼é«åº¦å±ä¸­
      if (hasL || hasR) {
        newLeft = hasL ? 0 : width;
        newTop = Math.floor(height / 2);
      }
    }

    const style = {
      marginLeft: hasR ? '-4px' : '-4px',
      marginTop: '-4px',
      left: `${newLeft}px`,
      top: `${newTop}px`,
      cursor: this.cursors[point],
    };

    return style;
  }

  getCursor() {
    const { angleToCursor, initialAngle, pointList } = this;
    const curComponent = this.componentDataService.curComponent;
    const rotate = (curComponent.style.rotate + 360) % 360; // é²æ­¢è§åº¦æè´æ°ï¼æä»¥ + 360
    const result = {};
    let lastMatchIndex = -1; // ä»ä¸ä¸ä¸ªå½ä¸­çè§åº¦çç´¢å¼å¼å§å¹éä¸ä¸ä¸ªï¼éä½æ¶é´å¤æåº¦
    pointList.forEach((point) => {
      const angle = (initialAngle[point] + rotate) % 360;
      const len = angleToCursor.length;
      while (true) {
        lastMatchIndex = (lastMatchIndex + 1) % len;
        const angleLimit = angleToCursor[lastMatchIndex];
        if (angle < 23 || angle >= 338) {
          result[point] = 'nw-resize';
          return;
        }

        if (angleLimit.start <= angle && angle < angleLimit.end) {
          result[point] = angleLimit.cursor + '-resize';
          return;
        }
      }
    });

    return result;
  }
}
