import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ComponentDataService } from '../../core/component/component-data.service';
import { ComponentBaseStyle } from '../../types/component-type';
import getStyle from '../../utils/style';

@Component({
  selector: 'editor',
  templateUrl: './editor.component.html',
  styleUrls: ['./editor.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class EditorComponent implements OnInit {
  isEdit: boolean = false;
  constructor(public componentDataService: ComponentDataService) {}

  ngOnInit(): void {}

  handleContextMenu(e) {
    e.stopPropagation();
    e.preventDefault();
    // è®¡ç®èåç¸å¯¹äºç¼è¾å¨çä½ç§»
    let target = e.target;
    let top = e.offsetY;
    let left = e.offsetX;
    while (!target.className.includes('editor')) {
      left += target.offsetLeft;
      top += target.offsetTop;
      target = target.parentNode;
    }
    this.componentDataService.contextmenu = {
      show: true,
      top,
      left,
    };
  }

  getComponentStyle(style: ComponentBaseStyle) {
    return getStyle(style, ['top', 'left', 'width', 'height', 'rotate']);
  }
}
