// Math.PI = 180 åº¦
function angleToRadian(angle) {
    return angle * Math.PI / 180
}

/**
 * è®¡ç®æ ¹æ®åå¿æè½¬åçç¹çåæ 
 * @param   {Object}  point  æè½¬åçç¹åæ 
 * @param   {Object}  center æè½¬ä¸­å¿
 * @param   {Number}  rotate æè½¬çè§åº¦
 * @return  {Object}         æè½¬åçåæ 
 * https://www.zhihu.com/question/67425734/answer/252724399 æè½¬ç©éµå¬å¼
 */
export function calculateRotatedPointCoordinate(point, center, rotate) {
    /**
     * æè½¬å¬å¼ï¼
     *  ç¹a(x, y)
     *  æè½¬ä¸­å¿c(x, y)
     *  æè½¬åç¹n(x, y)
     *  æè½¬è§åº¦Î¸                tan ??
     * nx = cosÎ¸ * (ax - cx) - sinÎ¸ * (ay - cy) + cx
     * ny = sinÎ¸ * (ax - cx) + cosÎ¸ * (ay - cy) + cy
     */

    return {
        x: (point.x - center.x) * Math.cos(angleToRadian(rotate)) - (point.y - center.y) * Math.sin(angleToRadian(rotate)) + center.x,
        y: (point.x - center.x) * Math.sin(angleToRadian(rotate)) + (point.y - center.y) * Math.cos(angleToRadian(rotate)) + center.y,
    }
}

/**
 * è·åæè½¬åçç¹åæ ï¼å«ä¸ªç¹ä¹ä¸ï¼
 * @param  {Object} style  æ ·å¼
 * @param  {Object} center ç»ä»¶ä¸­å¿ç¹
 * @param  {String} name   ç¹åç§°
 * @return {Object}        æè½¬åçç¹åæ 
 */
export function getRotatedPointCoordinate(style, center, name) {
    let point // point æ¯æªæè½¬åçåæ 
    switch (name) {
        case 't':
            point = {
                x: style.left + (style.width / 2),
                y: style.top,
            }

            break
        case 'b':
            point = {
                x: style.left + (style.width / 2),
                y: style.top + style.height,
            }

            break
        case 'l':
            point = {
                x: style.left,
                y: style.top + style.height / 2,
            }

            break
        case 'r':
            point = {
                x: style.left + style.width,
                y: style.top + style.height / 2,
            }

            break
        case 'lt':
            point = {
                x: style.left,
                y: style.top,
            }

            break
        case 'rt':
            point = {
                x: style.left + style.width,
                y: style.top,
            }

            break
        case 'lb':
            point = {
                x: style.left,
                y: style.top + style.height,
            }

            break
        default: // rb
            point = {
                x: style.left + style.width,
                y: style.top+ style.height,
            }
            
            break
    }

    return calculateRotatedPointCoordinate(point, center, style.rotate)
}

